import loading from '@/common/mixin/loading.js';
import uniNavBar from '@/components/uni-ui/uni-nav-bar/uni-nav-bar.vue';
import uniNumberBox from '@/components/uni-ui/uni-number-box/uni-number-box.vue';
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex';
import commonList from '@/components/common/common-list.vue';

export default {
	mixins: [loading],
	components: {
		uniNavBar,
		uniNumberBox,
		commonList
	},
	data() {
		return {
			isedit: false,
			hotList: []
		};
	},
	computed: {
		...mapState({
			list: state => state.cart.list,
			selectedList: state => state.cart.selectedList
		}),
		...mapGetters(['checkedAll', 'totalPrice', 'disableSelectAll'])
	},
	onLoad() {
		this.getData();
	},
	beforeDestroy() {
		uni.$off('updateCart');
	},
	onPullDownRefresh() {
		this.getData();
	},
	methods: {
		...mapActions(['doSelectAll', 'doDelGoods', 'doShowPopup', 'updateCartList']),
		...mapMutations(['selectItem', 'initCartList', 'unSelectAll']),
		changeNum(e, item, index) {
			if (item.num === e) return;
			uni.showLoading({
				title: '加载中...'
			});
			this.$H
				.post(
					'/cart/updatenumber/' + item.id,
					{
						num: e
					},
					{
						token: true
					}
				)
				.then(res => {
					console.log(res);
					item.num = e;
					uni.hideLoading();
				});
		},
		// 订单结算
		orderConfirm() {
			if (this.selectedList.length === 0) {
				return uni.showToast({
					title: '请选择要结算的商品',
					icon: 'none'
				});
			}
			uni.navigateTo({
				url: '../order-confirm/order-confirm?detail=' + JSON.stringify(this.selectedList)
			});
		},
		// 获取数据
		getData() {
			// 获取购物车数据
			this.updateCartList()
				.then(res => {
					uni.stopPullDownRefresh();
				})
				.catch(err => {
					uni.stopPullDownRefresh();
				});
			// 获取热门推荐
			this.$H.get('/goods/hotlist').then(res => {
				this.hotList = res.map(v => {
					return {
						id: v.id,
						cover: v.cover,
						title: v.title,
						desc: v.desc,
						oprice: v.min_oprice,
						pprice: v.min_price
					};
				});
			});
		}
	}
};
